import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AboutFilesComponent } from './about-files.component';
import { Routes, RouterModule } from '@angular/router';
import { FooterModule } from '../footer/footer.module';

const route:Routes = [
  {
    path:'',
    component: AboutFilesComponent
  }
]

@NgModule({
  declarations: [AboutFilesComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(route),
    FooterModule,
  ]
})
export class AboutFilesModule { }
